section#ativos{
    width: 100%;
    max-width: 930px;
    margin: 0 auto;
    padding: 0 30px;
    padding-top: 0px;
    margin-left: 150px;
    background: blueviolet;
    height: 180px;
    width: 28%;

}

section#ativos article{
    background: #fff;
    border: 1px solid #ddd;
    margin-bottom: 10px;
    background: chartreuse;
    width: 300px;
}

section#ativos .imgAtivos{
    margin-left: 10px;
    height: 50%;
    width: 40%;
    border: 20px;
    border-color: crimson;
}
section#ativos article header{
    padding: 10px;
    display: flex;
    align-items: center;

}

section#ativos article header .title{
    display: flex;
    flex-direction: column;
}

section#ativos article header span{
    font-size: 13px;
    padding-top: 5px;
}

section#ativos article >img{
    width: 10%;
}

section#ativos article footer{
    padding: 10px;
    word-wrap: break-word;

}
